// 1. String
export const shouldBeString = (name, value) => {
  if (typeof value !== 'string') throw new TypeError(`${name} should be a string`);
};

export const shouldBeNonEmptyString = (name, value) => {
  shouldBeString(name, value);
  if (value.length <= 0) throw new RangeError(`${name} should not be empty string`);
};

// 2. Number
export const shouldBeNumber = (name, value) => {
  if (typeof value !== 'number') throw new TypeError(`${name} should be a number`);
};

export const shouldBePositiveNumber = (name, value) => {
  shouldBeNumber(name, value);
  if (value <= 0) throw new RangeError(`${name} should be a positive number`);
};

export const shouldBeNonNegativeNumber = (name, value) => {
  shouldBeNumber(name, value);
  if (value < 0) throw new RangeError(`${name} should be a non-negative number`);
};

// 3. Object
export const shouldBeNonEmptyObject = (name, value) => {
  if (!(Object.keys(value).length > 0))
    throw new RangeError(`${name} should not be an empty array`);
};
