import { useEffect } from 'react';
import { useHistory, useLocation } from 'react-router-dom';

// material-ui
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';

// icons
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import PeopleIcon from '@material-ui/icons/People';
import WatchLaterIcon from '@material-ui/icons/WatchLater';

import AppStore from '../store/appStore';

const NAV_LIST = [
  ['/clients', '', PeopleIcon],
  // ['/inventory', '', VerticalSplitIcon], // '@material-ui/icons/VerticalSplit'
  ['/reminder', '', WatchLaterIcon],
  ['/account', '', AccountCircleIcon],
].map((item) => {
  const [pathname, label, icon] = item;
  return { pathname, label, icon };
});

const useInitNavgationByPathname = (action) => {
  const { pathname } = useLocation();
  useEffect(() => {
    const navIndex = NAV_LIST.findIndex((nav) => nav.pathname === pathname);
    action.changeBottomNavigationIndex(navIndex === -1 ? 0 : navIndex); // default is 0
  }, [action, pathname]);
};

const AppNavigation = () => {
  const action = AppStore.useAction();
  useInitNavgationByPathname(action);

  const nav = AppStore.useState((state) => state.bottomNavigation);
  const history = useHistory();

  return (
    <BottomNavigation
      showLabels
      value={nav.index}
      onChange={(event, navIndex) => {
        action.changeBottomNavigationIndex(navIndex);
        const nav = NAV_LIST[navIndex] || NAV_LIST[0]; // default is 0
        history.push(nav.pathname);
      }}
    >
      {NAV_LIST.map((nav, index) => (
        <BottomNavigationAction key={index} label={nav.label} icon={<nav.icon />} />
      ))}
    </BottomNavigation>
  );
};

export default AppNavigation;
