import { useHistory, useLocation } from 'react-router-dom';

import Firebase from '../apis/firebase';
import NuskinAPI from '../apis/nuskin';
import { useEffect } from 'react';

const useLogoutUnauthorizedUser = () => {
  const history = useHistory();
  const { pathname } = useLocation();
  const [isInitialized, isLogin] = Firebase.useState((state) => [
    !state.isInitializing,
    state.isLogin,
  ]);
  const isAuthPanel = !['/login', '/signup', '/forget_password'].includes(pathname);

  useEffect(() => {
    // auto logout
    if (isInitialized && isAuthPanel && !isLogin) {
      history.push('/login');
    }

    // auto login
    if (isInitialized && !isAuthPanel && isLogin) {
      history.push('/clients');
    }
  }, [history, isInitialized, isAuthPanel, isLogin, pathname]);
};

const useFetchNuskinProductList = () => {
  const dispatch = Firebase.useDispatch();

  useEffect(() => {
    let isMounted = true;

    dispatch((state) => {
      state.collection.product.isLoading = true;
    });

    NuskinAPI.getFilteredProductList()
      .then((list) => {
        if (!isMounted) return;

        dispatch((state) => {
          state.collection.product = { list, isLoading: false };

          // add item to the doc
          list.forEach((item) => {
            state.doc[item.id] = item;
          });
        });
      })
      .catch((e) => {
        console.log(e); // TODO: error handler

        if (!isMounted) return;
        dispatch((state) => {
          state.collection.product.isLoading = false;
        });
      });

    return () => {
      isMounted = false;
    };
  }, [dispatch]);
};

const useInitApp = () => {
  // fetch nuskin product and put into product collection
  useFetchNuskinProductList();

  // subscribe firestore
  Firebase.useSubscribeAuthStateChanged();
  Firebase.useSubscribeCollection('client');
  Firebase.useSubscribeCollection('purchase');
  Firebase.useSubscribeCollection('inventory');

  // logout unauthorized user to '/login' panel
  useLogoutUnauthorizedUser();
};

export default useInitApp;
