import { createContext, useContext, useReducer, useRef } from 'react';
import { createContext as createSelectorContext, useContextSelector } from 'use-context-selector';
import { produce } from 'immer';

const createAction = (ref, dispatch, actions) => {
  for (const [key, action] of Object.entries(actions)) {
    const type = Object.prototype.toString.call(action);

    if (type === '[object Function]') {
      ref[key] = (...param) => dispatch(action(...param));
    } else if (type === '[object Object]') {
      ref[key] = {};
      createAction(ref[key], dispatch, action);
    } else {
      throw new Error(
        `Unexpected action '${key}' (${type}). Only accpect types object and function.`
      );
    }
  }
};

/**
 * createStore
 * @param {object} initState
 * @param {object} actions
 * @returns
 */
const createStore = (initState = {}, actions = {}) => {
  // state and dispatch contexts
  const StateContext = createSelectorContext();
  const DispatchContext = createContext();
  const ActionContext = createContext();

  // Store Provider
  const Provider = ({ children, initState: _initState = initState }) => {
    const [state, dispatch] = useReducer(produce, _initState);

    // action ref
    const actionRef = useRef(null);
    if (actionRef.current === null) {
      actionRef.current = {};
      createAction(actionRef.current, dispatch, actions);
    }

    return (
      <StateContext.Provider value={state}>
        <DispatchContext.Provider value={dispatch}>
          <ActionContext.Provider value={actionRef.current}>{children}</ActionContext.Provider>
        </DispatchContext.Provider>
      </StateContext.Provider>
    );
  };

  return {
    Provider,
    useState: (selector = (state) => state) => useContextSelector(StateContext, selector),
    useDispatch: () => useContext(DispatchContext),
    useAction: () => useContext(ActionContext),
  };
};

export default createStore;
