import './App.scss';

import { Redirect, Route, Switch } from 'react-router-dom';
import useInitApp from '../hooks/useInitApp';

// authentication pages
import LoginPage from './authentication/LoginPage';
import SignupPage from './authentication/SignupPage';
import ForgetPasswordPage from './authentication/ForgetPasswordPage';

// client pages
import ClientsPage from './clients/ClientsPage';
import AddClientDialog from './clients/AddClientDialog';
import ClientInfoPage from './clients/ClientInfoPage';
import EditClientDialog from './clients/EditClientDialog';
import DeleteClientDialog from './clients/DeleteClientDialog';
import CreateOrderPage from './clients/CreateOrderPage';
import ProductPage from './product/ProductPage';

// inventory page
import InventoryPage from './inventory/InventoryPage';
import AddInventoryDialog from './inventory/AddInventoryDialog';

// reminder page
import ReminderPage from './reminder/ReminderPage';
import AddPurchaseDialog from './reminder/AddPurchaseDialog';

// account page
import AccountPage from './account/AccountPage';
import SubscriptionRuleDialog from './account/SubscriptionRuleDialog';

function App() {
  useInitApp();

  return (
    <div id="reminder-app">
      <Switch>
        <Route exact path="/login">
          <LoginPage />
        </Route>

        <Route exact path="/signup">
          <SignupPage />
        </Route>

        <Route exact path="/forget_password">
          <ForgetPasswordPage />
        </Route>

        <Route exact path="/clients">
          <ClientsPage />
          <AddClientDialog />
        </Route>

        <Route exact path="/clients/:clientId">
          <ClientInfoPage />
          <DeleteClientDialog />
          <EditClientDialog />
        </Route>

        <Route exact path="/clients/:clientId/order/quantities">
          <CreateOrderPage />
        </Route>

        <Route exact path="/inventory">
          <InventoryPage />
          <AddInventoryDialog />
        </Route>

        <Route exact path="/reminder">
          <ReminderPage />
          <AddPurchaseDialog />
        </Route>

        <Route exact path="/account">
          <AccountPage />
          <SubscriptionRuleDialog />
        </Route>

        <Route exact path="/clients/:clientId/order/selectProducts">
          <ProductPage />
        </Route>

        <Redirect to="/login" />
      </Switch>
    </div>
  );
}

export default App;
