const setSearchInput = (searchInput) => (state) => {
  state.client.searchInput = searchInput.toLowerCase();
};

const showAddDialog = (show) => (state) => {
  state.client.showAddDialog = show;
};

const showEditDialog = (show) => (state) => {
  state.client.showEditDialog = show;
};

const showDeleteDialog = (show) => (state) => {
  state.client.showDeleteDialog = show;
};

const clientActions = {
  setSearchInput,
  showAddDialog,
  showEditDialog,
  showDeleteDialog,
};

export default clientActions;
