// material-ui
import Fab from '@material-ui/core/Fab';

// data-grid
import { DataGrid } from '@material-ui/data-grid';

// icon
import AddIcon from '@material-ui/icons/Add';

import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';

import AppNavigation from '../AppNavigation';
import Loader from '../Loader';

// prettier-ignore
const columns = [
  { width: 270, field: 'product', headerName: '' },
  { width: 70,  field: 'amount',  headerName: '', type: 'number' },
];

const InventoryPage = () => {
  const action = AppStore.useAction();
  const inventory = Firebase.useCollection('inventory');
  const doc = Firebase.useDocument();

  // check loading
  const isDataLoading = Firebase.useIsLoading();
  if (isDataLoading) {
    return <Loader />;
  }

  const rows = inventory.list.map((item) => {
    const product = doc(item.productId);

    return {
      id: item.id,
      product: product.name,
      amount: item.amount,
      price: product.price,
    };
  });

  return (
    <>
      <div className="panel-container">
        <div className="panel-content">
          <DataGrid rows={rows} columns={columns} autoHeight pageSize={9} />
        </div>
        <AppNavigation />
      </div>
      <Fab
        className="add-float-button"
        color="primary"
        onClick={() => action.showAddInventoryDialog(true)}
      >
        <AddIcon />
      </Fab>
    </>
  );
};

export default InventoryPage;
