import { useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';

// material-ui
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';
import Loader from '../Loader';

const DeleteClientDialog = () => {
  const showDeleteClientDialog = AppStore.useState((state) => state.client.showDeleteDialog);
  const action = AppStore.useAction();
  const { clientId } = useParams();
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(false);

  const deleteClient = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      await Firebase.collection('client').doc(clientId).delete();
    } catch (err) {
      console.log(err);
    }
    action.client.showDeleteDialog(false);
    setIsLoading(false);
    // TODO: show another dialog?
    history.push('/clients/');
  };

  return (
    <Dialog open={showDeleteClientDialog} onClose={() => action.client.showDeleteDialog(false)}>
      <DialogTitle></DialogTitle>
      <DialogContent>
        <DialogContentText></DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button color="primary" onClick={() => action.client.showDeleteDialog(false)}>
          
        </Button>
        <Button color="secondary" autoFocus onClick={deleteClient}>
          
        </Button>
      </DialogActions>

      <Loader open={isLoading} />
    </Dialog>
  );
};

export default DeleteClientDialog;
