import { useState } from 'react';

// material-ui
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionActions from '@material-ui/core/AccordionActions';
import Button from '@material-ui/core/Button';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import Typography from '@material-ui/core/Typography';

const ClientAccordions = ({ data }) => {
  const [checked, setChecked] = useState([]);

  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  const clients = Object.entries(data).map(([clientName, clientPurchases]) => {
    const lists = Object.entries(clientPurchases).map(([purchaseDate, purchases]) => {
      return (
        <AccordionDetails key={purchaseDate}>
          <Typography>{purchaseDate}</Typography>
          <List>
            {purchases.map((purchase) => (
              <ListItem key={purchase.id} button onClick={handleToggle(purchase.id)}>
                <ListItemIcon>
                  <Checkbox checked={checked.indexOf(purchase.id) !== -1} />
                </ListItemIcon>
                <ListItemText primary={purchase.product} secondary={`* ${purchase.amount}`} />
              </ListItem>
            ))}
          </List>
        </AccordionDetails>
      );
    });

    return (
      <Accordion key={clientName}>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography>{clientName}</Typography>
        </AccordionSummary>
        {lists}
        <AccordionActions>
          <Button variant="contained" size="small" color="primary">
            
          </Button>
        </AccordionActions>
      </Accordion>
    );
  });

  if (clients.length === 0) {
    return <Typography></Typography>;
  }

  return clients;
};

export default ClientAccordions;
