import { useState } from 'react';
import { useHistory } from 'react-router-dom';

// material-ui
import Button from '@material-ui/core/Button';
import FormHelperText from '@material-ui/core/FormHelperText';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import Firebase from '../../apis/firebase';
import Loader from '../Loader';

const getErrorMessage = (error) => {
  const ERROR_MESSAGE = {
    'auth/invalid-email': '',
    'auth/email-already-in-use': '',
    'auth/weak-password': '',
  };
  return ERROR_MESSAGE[error.code] || `${error.code}: ${error.message}`;
};

const SignupPage = () => {
  const history = useHistory();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [isSignuping, setIsSignuping] = useState(false);
  const [error, setError] = useState({ code: '', message: '' });
  const isConfirmPasswordError =
    password !== '' && confirmPassword !== '' && password !== confirmPassword;

  const onFieldChange = (stateSetter) => (e) => {
    setError({ code: '', message: '' });
    stateSetter(e.target.value);
  };

  return (
    <form
      style={{ textAlign: 'center', padding: '10px' }}
      onSubmit={async (e) => {
        e.preventDefault();

        setIsSignuping(true);
        await Firebase.signup(email, password).catch(setError);
        setIsSignuping(false);
      }}
    >
      <Loader open={isSignuping} />

      <br />
      <br />
      <Typography variant="h3" gutterBottom>
        Reminder
      </Typography>

      {/* 1. email */}
      <TextField
        style={{ width: '100%' }}
        name="email"
        type="email"
        label=""
        variant="outlined"
        InputLabelProps={{ shrink: true }}
        onChange={onFieldChange(setEmail)}
        error={/email/.test(error.code)}
      />

      <br />
      <br />

      {/* 2. password */}
      <TextField
        style={{ width: '100%' }}
        name="password"
        type="password"
        label=""
        variant="outlined"
        InputLabelProps={{ shrink: true }}
        onChange={onFieldChange(setPassword)}
        error={/password/.test(error.code)}
      />

      <br />
      <br />

      {/* 3. confirm password */}
      <TextField
        style={{ width: '100%' }}
        name="confirm-password"
        type="password"
        label=""
        variant="outlined"
        InputLabelProps={{ shrink: true }}
        onChange={onFieldChange(setConfirmPassword)}
        error={isConfirmPasswordError}
        helperText={isConfirmPasswordError && ''}
      />

      <br />
      <br />

      {error.code && <FormHelperText error={true}>{getErrorMessage(error)}</FormHelperText>}
      {error.code && <br />}

      <div className="unauth-buttons">
        <Button size="large" color="primary" onClick={() => history.push('/login')}>
          
        </Button>

        <Button
          variant="contained"
          size="large"
          type="submit"
          color="primary"
          disabled={email === '' || password === '' || password !== confirmPassword}
        >
          
        </Button>
      </div>
    </form>
  );
};

export default SignupPage;
