import React from 'react';
import { useHistory, useParams } from 'react-router-dom';

// react-window
import { FixedSizeList } from 'react-window';
import AutoSizer from 'react-virtualized-auto-sizer';

// app bar
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIosTwoToneIcon from '@material-ui/icons/ArrowBackIosTwoTone';

// list items
import Avatar from '@material-ui/core/Avatar';
import Checkbox from '@material-ui/core/Checkbox';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

// store
import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';

import Loader from '../Loader';
import SearchBox from '../SearchBox';

const ProductItemRenderer = ({ data, index, style }) => {
  const { selectedItems, toggleProduct, productList } = data;
  const product = productList[index];

  return (
    <ListItem button style={style} key={index} onClick={toggleProduct(product)}>
      <ListItemIcon>
        <Checkbox color="primary" checked={selectedItems.hasOwnProperty(product.id)} />
      </ListItemIcon>
      <ListItemAvatar>
        <Avatar src={product.img} />
      </ListItemAvatar>
      <ListItemText primary={product.name} />
    </ListItem>
  );
};

const ProductPage = () => {
  const history = useHistory();
  const action = AppStore.useAction();
  const product = Firebase.useCollection('product');
  const productPage = AppStore.useState((s) => s.productPage);
  const param = useParams();
  const doc = Firebase.useDocument();

  // check loading
  const isDataLoading = Firebase.useIsLoading();
  if (isDataLoading) {
    return <Loader />;
  }

  // check client
  const client = doc(param.clientId);
  if (!client) {
    return <Typography variant="h4"></Typography>;
  }

  // product list
  const filteredProductList = product.list.filter((p) => {
    return p.name.toLowerCase().includes(productPage.searchInput);
  });

  const gotoCreateOrderPage = () => {
    history.push(`/clients/${client.id}/order/quantities`);
  };

  const gotoClientInfoPage = () => {
    action.product.setSearchInput('');
    action.product.clearSelectedProducts();
    history.push(`/clients/${client.id}`);
  };

  return (
    <div className="panel-container">
      <AppBar position="static">
        <Toolbar>
          <IconButton
            color="inherit"
            onClick={() => {
              gotoClientInfoPage();
            }}
          >
            <ArrowBackIosTwoToneIcon />
          </IconButton>
          <Typography variant="h6"></Typography>
          <SearchBox
            searchInput={productPage.searchInput}
            onChange={(e) => action.product.setSearchInput(e.target.value)}
          />
          <IconButton
            color="inherit"
            onClick={() => {
              gotoCreateOrderPage();
            }}
          >
            <ShoppingCartIcon />
          </IconButton>

          <Button onClick={action.product.clearSelectedProducts} color="secondary">
            
          </Button>
        </Toolbar>
      </AppBar>

      <div className="panel-content">
        <AutoSizer>
          {({ height, width }) => (
            <FixedSizeList
              itemData={{
                selectedItems: productPage.selectedItems,
                toggleProduct: (product) => () => action.product.toggleProduct(product),
                productList: filteredProductList,
              }}
              height={height}
              width={width}
              itemSize={50}
              itemCount={filteredProductList.length}
            >
              {ProductItemRenderer}
            </FixedSizeList>
          )}
        </AutoSizer>
      </div>
    </div>
  );
};

export default ProductPage;
