import { useState } from 'react';
import { useHistory } from 'react-router-dom';

// material-ui
import Button from '@material-ui/core/Button';
import FormHelperText from '@material-ui/core/FormHelperText';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import { AppBar, Dialog, IconButton, Toolbar } from '@material-ui/core';

import Firebase from '../../apis/firebase';
import Loader from '../Loader';

const getErrorMessage = (error) => {
  const ERROR_MESSAGE = {
    'auth/invalid-email': '',
    'auth/user-not-found': '',
    'auth/too-many-requests': '',
  };
  return ERROR_MESSAGE[error.code] || `${error.code}: ${error.message}`;
};

function ShowSuccessDialog(props) {
  const open = props.open;
  const onClose = props.onClose;
  return (
    <Dialog open={open} onClose={onClose}>
      <AppBar style={{ position: 'relative' }}>
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={onClose} aria-label="close">
            <CloseIcon />
          </IconButton>
          <Typography variant="h6" style={{ flex: 1 }}>
            
          </Typography>
        </Toolbar>
      </AppBar>
    </Dialog>
  );
}

const ForgetPasswordPage = () => {
  const history = useHistory();
  const [email, setEmail] = useState('');
  const [isSignuping, setIsSignuping] = useState(false);
  const [error, setError] = useState({ code: '', message: '' });
  const [openDialog, setOpenDialog] = useState(false);

  const onFieldChange = (stateSetter) => (e) => {
    setError({ code: '', message: '' });
    stateSetter(e.target.value);
  };

  return (
    <form
      style={{ textAlign: 'center', padding: '10px' }}
      onSubmit={async (e) => {
        e.preventDefault();

        setIsSignuping(true);
        await Firebase.sendPasswordResetEmail(email)
          .then(() => {
            setOpenDialog(true);
          })
          .catch(setError);
        setIsSignuping(false);
      }}
    >
      <ShowSuccessDialog open={openDialog} onClose={() => setOpenDialog(false)} />
      <Loader open={isSignuping} />

      <br />
      <br />
      <Typography variant="h3" gutterBottom>
        Reminder
      </Typography>

      {/* 1. email */}
      <TextField
        style={{ width: '100%' }}
        name="email"
        type="email"
        label=""
        variant="outlined"
        InputLabelProps={{ shrink: true }}
        onChange={onFieldChange(setEmail)}
        error={/email/.test(error.code)}
      />

      <br />
      <br />

      {error.code && <FormHelperText error={true}>{getErrorMessage(error)}</FormHelperText>}
      {error.code && <br />}

      <div className="unauth-buttons">
        <Button size="large" color="primary" onClick={() => history.push('/login')}>
          
        </Button>

        <Button
          variant="contained"
          size="large"
          type="submit"
          color="primary"
          disabled={email === ''}
        >
          
        </Button>
      </div>
    </form>
  );
};

export default ForgetPasswordPage;
