import { forwardRef, useEffect, useRef, useState } from 'react';
import { useParams } from 'react-router-dom';

// material-ui
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import IconButton from '@material-ui/core/IconButton';
import Slide from '@material-ui/core/Slide';
import TextField from '@material-ui/core/TextField';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

// icon
import CloseIcon from '@material-ui/icons/Close';

import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';
import Loader from '../Loader';
import { requestSubmit } from '../../utils/generalUtils';

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const EditClientDialog = () => {
  const action = AppStore.useAction();
  const [isLoading, setIsLoading] = useState(false);
  const showEditClientDialog = AppStore.useState((s) => s.client.showEditDialog);
  const formRef = useRef(null);
  const [formValues, setFormValues] = useState({});
  const { clientId } = useParams();
  const doc = Firebase.useDocument();

  const clientInfo = doc(clientId);
  useEffect(() => {
    if (!clientInfo) return;

    const { id, ...info } = clientInfo;
    setFormValues(info);
  }, [clientInfo]);

  // check loading
  const isDataLoading = Firebase.useIsLoading();
  if (isDataLoading) {
    return <Loader />;
  }

  const onFieldChange = (e) => {
    setFormValues({
      ...formValues,
      [e.target.name]: e.target.value,
    });
  };

  const onSubmit = async (e) => {
    e.preventDefault();

    setIsLoading(true);
    await Firebase.collection('client').doc(clientId).set(formValues);
    setIsLoading(false);
    // TODO: another dialog to show the edit is success?
    action.client.showEditDialog(false);
  };

  return (
    <Dialog
      fullScreen
      open={showEditClientDialog}
      onClose={() => action.client.showEditDialog(false)}
      TransitionComponent={Transition}
    >
      <AppBar style={{ position: 'relative' }}>
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            onClick={() => action.client.showEditDialog(false)} // TODO: dirty check
            aria-label="close"
          >
            <CloseIcon />
          </IconButton>
          <Typography variant="h6" style={{ flex: 1 }}>
            
          </Typography>
          <Button autoFocus color="inherit" onClick={() => requestSubmit(formRef.current)}>
            
          </Button>
        </Toolbar>
      </AppBar>

      <form style={{ padding: '30px 24px' }} ref={formRef} onSubmit={onSubmit}>
        <Loader open={isLoading} />

        {/* 1. lastName */}
        <TextField
          name="lastName"
          value={formValues.lastName}
          label=""
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
        />

        <br />
        <br />

        {/* 2. firstName */}
        <TextField
          required
          name="firstName"
          value={formValues.firstName}
          label=""
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
        />

        <br />
        <br />

        {/* 3. phone */}
        <TextField
          name="phone"
          value={formValues.phone}
          type="phone"
          label=""
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
        />

        <br />
        <br />

        {/* 4. email */}
        <TextField
          name="email"
          value={formValues.email}
          type="email"
          label=""
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
        />

        <br />
        <br />

        {/* 5. city */}
        <TextField
          name="city"
          value={formValues.city}
          label=""
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
        />

        <br />
        <br />

        {/* 6. birth */}
        <TextField
          name="birth"
          value={formValues.birth}
          label=""
          type="date"
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
          style={{ width: '210px' }}
        />

        <br />
        <br />

        {/* 7. note */}
        <TextField
          name="note"
          value={formValues.note}
          label=""
          multiline
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
          style={{ width: '100%' }}
        />
      </form>
    </Dialog>
  );
};

export default EditClientDialog;
