// material-ui
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import Firebase from '../../apis/firebase';
import AppStore from '../../store/appStore';
import { formatDate } from '../../utils/generalUtils';

const SubscriptionStatus = () => {
  const action = AppStore.useAction();
  const user = Firebase.useState((state) => state.user);
  const isSubscribed = user.expirationDate >= formatDate(new Date());

  // Subscribed
  if (isSubscribed) {
    return (
      <Card>
        <CardContent>
          <Typography variant="h5" component="h2">
            
          </Typography>
          <br />
          {user.expirationDate}
        </CardContent>
        <CardActions>
          <Button color="primary"></Button>
        </CardActions>
      </Card>
    );
  }

  // Unsubscribed
  return (
    <Card>
      <CardContent>
        <Typography variant="h5" component="h2">
          
        </Typography>
      </CardContent>
      <CardActions>
        <Button
          color="primary"
          onClick={async () => {
            // TODO: move this function into payment.js
            const ecpay = Firebase.firebase.functions().httpsCallable('ecpay');

            // TODO: remove unused parameters
            const result = await ecpay({
              uid: user.uid,
              email: user.email,
              ClientBackURL: window.location.href,
              OrderResultURL: window.location.href,
            });

            // forward to ecapy's website
            document.write(result.data.html_form);
          }}
        >
          
        </Button>
        <Button disabled color="primary" onClick={() => action.showSubscriptionRuleDialog(true)}>
          
        </Button>
      </CardActions>
    </Card>
  );
};

export default SubscriptionStatus;
