import { forwardRef, useRef, useState } from 'react';

// material-ui
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import FormControl from '@material-ui/core/FormControl';
import IconButton from '@material-ui/core/IconButton';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Slide from '@material-ui/core/Slide';
import TextField from '@material-ui/core/TextField';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

// icon
import CloseIcon from '@material-ui/icons/Close';

import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';
import Loader from '../Loader';
import { formatDate, requestSubmit } from '../../utils/generalUtils';

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const DefaultFormValues = {
  clientId: '', // required
  productId: '', // required
  amount: '1', // required (min: 1)
  purchaseDate: formatDate(new Date()), // required
  note: '',
};

const AddPurchaseDialog = () => {
  const action = AppStore.useAction();
  const [isLoading, setIsLoading] = useState(false);
  const addPurchaseDialog = AppStore.useState((state) => state.addPurchaseDialog);
  const client = Firebase.useCollection('client');
  const product = Firebase.useCollection('product');

  const formRef = useRef(null);
  const [formValues, setFormValues] = useState(DefaultFormValues);

  const onFieldChange = (e) => {
    setFormValues({
      ...formValues,
      [e.target.name]: e.target.value,
    });
  };

  const onSubmit = async (e) => {
    e.preventDefault();

    setIsLoading(true);
    await Firebase.collection('purchase').add({
      ...formValues,
      amount: Number.parseInt(formValues.amount, 10),
    });
    setFormValues(DefaultFormValues);
    setIsLoading(false);

    action.showAddPurchaseDialog(false);
  };

  return (
    <Dialog
      fullScreen
      open={addPurchaseDialog.show}
      onClose={() => action.showAddPurchaseDialog(false)}
      TransitionComponent={Transition}
    >
      <AppBar style={{ position: 'relative' }}>
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            onClick={() => action.showAddPurchaseDialog(false)} // TODO: dirty check
            aria-label="close"
          >
            <CloseIcon />
          </IconButton>
          <Typography variant="h6" style={{ flex: 1 }}>
            
          </Typography>
          <Button autoFocus color="inherit" onClick={() => requestSubmit(formRef.current)}>
            
          </Button>
        </Toolbar>
      </AppBar>

      <form style={{ padding: '30px 24px' }} ref={formRef} onSubmit={onSubmit}>
        <Loader open={isLoading} />

        {/* 1. clientId */}
        <FormControl required variant="outlined" style={{ width: '100%' }}>
          <InputLabel shrink></InputLabel>
          <Select name="clientId" value={formValues.clientId} onChange={onFieldChange}>
            {client.list.map((client) => (
              <MenuItem key={client.id} value={client.id}>
                {client.lastName + client.firstName}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <br />
        <br />

        {/* 2. productId */}
        <FormControl required variant="outlined" style={{ width: '100%' }}>
          <InputLabel shrink></InputLabel>
          <Select name="productId" value={formValues.productId} onChange={onFieldChange}>
            {product.list.map((product) => (
              <MenuItem key={product.id} value={product.id}>
                {product.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <br />
        <br />

        {/* 3. amount */}
        <TextField
          required
          name="amount"
          value={formValues.amount}
          label=""
          variant="outlined"
          type="number"
          inputProps={{ min: 1 }}
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
          style={{ width: '100%' }}
        />
        <br />
        <br />

        {/* 4. purchase date */}
        <TextField
          required
          name="purchaseDate"
          value={formValues.purchaseDate}
          label=""
          type="date"
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
          style={{ width: '210px' }}
        />
        <br />
        <br />

        {/* 5. note */}
        <TextField
          name="note"
          value={formValues.note}
          label=""
          multiline
          variant="outlined"
          InputLabelProps={{ shrink: true }}
          onChange={onFieldChange}
          style={{ width: '100%' }}
        />
      </form>
    </Dialog>
  );
};

export default AddPurchaseDialog;
