import {
  shouldBeString,
  shouldBeNonEmptyString,
  shouldBePositiveNumber,
  shouldBeNonEmptyObject,
} from './validator';

/**
 * createPurchase
 *
 * @param {object} purchase
 * @param {string} purchase.orderId
 * @param {string} purchase.clientId
 * @param {string} purchase.productId
 * @param {number} purchase.amount
 * @param {date}   purchase.date
 * @param {Array}  purchase.remindDates
 * @param {string} purchase.note
 * @returns
 */
const createPurchase = ({
  orderId,
  clientId,
  productId,
  amount,
  purchaseDate,
  remindDates,
  note,
}) => {
  shouldBeNonEmptyString('orderId', orderId);
  shouldBeNonEmptyString('clientId', clientId);
  shouldBeNonEmptyString('productId', productId);
  shouldBePositiveNumber('amount', amount);
  shouldBeNonEmptyString('date', purchaseDate);
  shouldBeNonEmptyObject('remindDates', remindDates);
  shouldBeString('note', note);
  return { orderId, clientId, productId, amount, purchaseDate, remindDates, note };
};

const schema = {
  createPurchase,
};

export default schema;
