import { useHistory } from 'react-router-dom';

// material-ui
import AppBar from '@material-ui/core/AppBar';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Toolbar from '@material-ui/core/Toolbar';

// icon
import AddIcon from '@material-ui/icons/Add';

// store
import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';

import AppNavigation from '../AppNavigation';
import Loader from '../Loader';
import SearchBox from '../SearchBox';

const ClientItem = ({ client, purchase }) => {
  const history = useHistory();

  const [lastPurchaseDate = ''] = purchase.list
    .filter((purchase) => purchase.clientId === client.id)
    .map((purchase) => purchase.purchaseDate)
    .sort((a, b) => (a < b ? 1 : a > b ? -1 : 0)); // sort string by large to small order

  const birth = client.birth ? ` (${client.birth})` : '';

  return (
    <ListItem button onClick={() => history.push('/clients/' + client.id)}>
      <ListItemAvatar>
        <Avatar></Avatar>
      </ListItemAvatar>
      <ListItemText
        primary={client.lastName + client.firstName + birth}
        secondary={'' + lastPurchaseDate}
      />
    </ListItem>
  );
};

const ClientsPage = () => {
  const action = AppStore.useAction();
  const client = Firebase.useCollection('client');
  const purchase = Firebase.useCollection('purchase');
  const searchInput = AppStore.useState((s) => s.client.searchInput);

  // check loading
  const isDataLoading = Firebase.useIsLoading();
  if (isDataLoading) {
    return <Loader />;
  }

  const clientList = client.list
    .filter((c) => (c.lastName + c.firstName).toLowerCase().includes(searchInput))
    .map((c) => <ClientItem key={c.id} client={c} purchase={purchase} />);

  return (
    <div className="panel-container">
      <AppBar position="static">
        <Toolbar>
          <SearchBox
            searchInput={searchInput}
            onChange={(e) => action.client.setSearchInput(e.target.value)}
          />
          <div style={{ flexGrow: 1 }}></div>
          <IconButton color="inherit" onClick={() => action.client.showAddDialog(true)}>
            <AddIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <div className="panel-content">
        <List>{clientList}</List>
      </div>
      <AppNavigation />
    </div>
  );
};

export default ClientsPage;
