import { forwardRef, useState } from 'react';

// material-ui
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import Slide from '@material-ui/core/Slide';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

import AppStore from '../../store/appStore';

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const SubscriptionRuleDialog = () => {
  const showSubscriptionRuleDialog = AppStore.useState(
    (state) => state.subscriptionRuleDialog.show
  );

  const action = AppStore.useAction();

  const [isBottomed, setIsBottomed] = useState(false);

  const handleScroll = (event) => {
    const target = event.target;

    if (target.scrollHeight - target.scrollTop === target.clientHeight) {
      setIsBottomed(true);
    }
  };

  return (
    <Dialog
      fullScreen
      open={showSubscriptionRuleDialog}
      onClose={() => action.showSubscriptionRuleDialog(false)}
      TransitionComponent={Transition}
    >
      <AppBar style={{ position: 'relative' }}>
        <Toolbar>
          <Typography variant="h6" style={{ flex: 1 }}>
            
          </Typography>
        </Toolbar>
      </AppBar>

      <DialogContent onScroll={handleScroll}>
        <DialogContentText></DialogContentText>
        <DialogContentText></DialogContentText>
        <DialogContentText></DialogContentText>
        <DialogContentText></DialogContentText>
        <DialogContentText>
          
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        {/* TODO: https://github.com/zlargon/reminder-app/issues/46 */}
        <Button
          disabled={!isBottomed}
          size="large"
          variant="contained"
          color="primary"
          onClick={() => {
            action.showSubscriptionRuleDialog(false);
            setIsBottomed(false);
          }}
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default SubscriptionRuleDialog;
