import { useHistory, useParams } from 'react-router-dom';
import { nanoid } from 'nanoid';

// material-ui
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionActions from '@material-ui/core/AccordionActions';
import AppBar from '@material-ui/core/AppBar';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

// icon
import ArrowBackIosTwoToneIcon from '@material-ui/icons/ArrowBackIosTwoTone';
import DeleteIcon from '@material-ui/icons/Delete';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';
import Loader from '../Loader';

export default function ClientInfoPage() {
  const history = useHistory();
  const { clientId } = useParams();
  const purchaseCollection = Firebase.useCollection('purchase');
  const doc = Firebase.useDocument();
  const action = AppStore.useAction();

  // check loading
  const isDataLoading = Firebase.useIsLoading();
  if (isDataLoading) {
    return <Loader />;
  }

  // check client
  const client = doc(clientId);
  if (!client) {
    return <Typography variant="h4"></Typography>;
  }

  const groupedPurchaseData = {};
  purchaseCollection.list
    .filter((purchase) => purchase.clientId === clientId)
    .forEach((purchase) => {
      const newDate = purchase.purchaseDate;
      const newOrder = purchase.orderId;
      const key = newDate + newOrder;
      if (typeof groupedPurchaseData[key] === 'undefined') {
        groupedPurchaseData[key] = [];
      }
      groupedPurchaseData[key].push(purchase);
    });

  const orderedGroupedPurchaseData = Object.keys(groupedPurchaseData)
    .sort((a, b) => new Date(b.slice(0, 10)) - new Date(a.slice(0, 10)))
    .reduce((arr, key) => {
      arr.push(groupedPurchaseData[key]);
      return arr;
    }, []);

  const renderPurchaseData = orderedGroupedPurchaseData.map((order) => {
    let orderValue = 0;
    const renderedList = order.map((purchase) => {
      const product = doc(purchase.productId);
      orderValue += product.price * purchase.amount;
      return (
        <ListItem button key={`item-${purchase.id}`}>
          <ListItemAvatar className="product-item-avatar">
            <Avatar src={product.img} />
          </ListItemAvatar>

          <ListItemText
            primary={product.name}
            secondary={`$${product.price} x ${purchase.amount}`}
          ></ListItemText>
        </ListItem>
      );
    });

    const key = nanoid();
    return (
      <Accordion key={`order-${key}`}>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
          <Typography style={{ flexBasis: '50%' }}>{order[0].purchaseDate}</Typography>
          <Typography>{`$${orderValue}`}</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <List>{renderedList}</List>
        </AccordionDetails>
        <AccordionActions>
          <Button size="small" variant="contained" color="primary">
            
          </Button>
        </AccordionActions>
      </Accordion>
    );
  });

  return (
    <div className="panel-container">
      <AppBar position="static">
        <Toolbar>
          <IconButton color="inherit" onClick={() => history.push('/clients/')}>
            <ArrowBackIosTwoToneIcon />
          </IconButton>
          <Typography variant="h6">{client.lastName + client.firstName}</Typography>
          <div style={{ flexGrow: 1 }}></div>
          <IconButton color="inherit">
            <DeleteIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <div className="panel-content">
        {/* 1. Basic Information */}
        <Accordion>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography></Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div style={{ width: '100%' }}>
              <Typography>{client.lastName + client.firstName}</Typography>
              <Typography>{client.birth}</Typography>
              <Typography>{client.phone}</Typography>
              <Typography>{client.email}</Typography>
              <Typography>{client.city}</Typography>
              <Typography>{client.note}</Typography>
              <div style={{ display: 'flex' }}>
                <div style={{ flexGrow: 1 }}></div>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => action.client.showEditDialog(true)}
                >
                  
                </Button>
              </div>
            </div>
          </AccordionDetails>
        </Accordion>

        {/* 2. Purchase History */}
        <br />
        <br />
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <Typography variant="h6"></Typography>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              history.push(`/clients/${clientId}/order/selectProducts`);
            }}
          >
            
          </Button>
        </div>
        <br />
        {renderPurchaseData}
      </div>
    </div>
  );
}
