const setSearchInput = (searchInput) => (state) => {
  state.productPage.searchInput = searchInput.toLowerCase();
};

const toggleProduct = (product) => (state) => {
  if (state.productPage.selectedItems.hasOwnProperty(product.id)) {
    delete state.productPage.selectedItems[product.id];
  } else {
    state.productPage.selectedItems[product.id] = true;
  }
};

const clearSelectedProducts = () => (state) => {
  state.productPage.selectedItems = {};
};

const productActions = {
  setSearchInput,
  toggleProduct,
  clearSelectedProducts,
};

export default productActions;
