import createStore from '../utils/createStore';
import clientActions from '../components/clients/clientActions';
import productActions from '../components/product/productActions';

// App State
const appState = {
  client: {
    searchInput: '',
    showAddDialog: false,
    showDeleteDialog: false,
    showEditDialog: false,

    order: {
      orderList: [],
      searchProductIntput: '',
      selectedProductItems: {},
    },
  },

  productPage: {
    searchInput: '',
    selectedItems: {},
  },
  addInventoryDialog: { show: false },
  addPurchaseDialog: { show: false },
  subscriptionRuleDialog: { show: false },
  bottomNavigation: { index: 0 },
};

// App Actions
// prettier-ignore
const changeBottomNavigationIndex = (index = 0) => (state) => {
  state.bottomNavigation.index = index;
};

const showAddInventoryDialog = (show) => (state) => {
  state.addInventoryDialog.show = show;
};

const showAddPurchaseDialog = (show) => (state) => {
  state.addPurchaseDialog.show = show;
};

const showSubscriptionRuleDialog = (show) => (state) => {
  state.subscriptionRuleDialog.show = show;
};

const appActions = {
  client: clientActions,
  product: productActions,
  changeBottomNavigationIndex,
  showAddInventoryDialog,
  showAddPurchaseDialog,
  showSubscriptionRuleDialog,
};

// App Store
export default createStore(appState, appActions);
