import './CreateOrderPage.scss';
import { useState, useEffect } from 'react';
import produce from 'immer';
import { useHistory, useParams } from 'react-router-dom';
import { nanoid } from 'nanoid';

// material-ui
import AppBar from '@material-ui/core/AppBar';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

// icon
import ArrowBackIosTwoToneIcon from '@material-ui/icons/ArrowBackIosTwoTone';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

import AppStore from '../../store/appStore';
import Firebase from '../../apis/firebase';
import Loader from '../Loader';
import Schema from '../../utils/schema';
import { formatDate } from '../../utils/generalUtils';
import moment from 'moment';

const OrderItem = ({ order, orderIndex, setOrderList }) => {
  const { product, amount } = order;
  const action = AppStore.useAction();

  const updateItemAmount = (amount) => {
    return setOrderList((orderList) => {
      return produce(orderList, (draft) => {
        const newAmount = draft[orderIndex].amount + amount;
        if (newAmount < 0) {
          draft.splice(orderIndex, 1); // delete order
          action.product.toggleProduct(orderList[orderIndex].product);
        } else {
          draft[orderIndex].amount = newAmount; // update amount
        }
      });
    });
  };

  return (
    <ListItem>
      <ListItemAvatar className="product-item-avatar">
        <Avatar src={product.img} />
      </ListItemAvatar>

      <ListItemText
        className="product-item-text"
        primary={product.name}
        secondary={product.catalog.name}
      />

      <ListItemSecondaryAction className="product-item-amount">
        <Typography className="amount">
          ${product.price} x {amount}
        </Typography>

        <ButtonGroup color="primary" aria-label="outlined primary button group">
          <Button onClick={() => updateItemAmount(+1)}>+</Button>
          <Button onClick={() => updateItemAmount(-1)}>-</Button>
        </ButtonGroup>
      </ListItemSecondaryAction>
    </ListItem>
  );
};

const CreateOrderPage = () => {
  // hooks
  const history = useHistory();
  const param = useParams();
  const doc = Firebase.useDocument();
  const productPage = AppStore.useState((s) => s.productPage);
  const action = AppStore.useAction();
  const [orderList, setOrderList] = useState([]);
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [isSavingOrderList, setIsSavingOrderList] = useState(false);

  useEffect(() => {
    const chosenProducts = Object.keys(productPage.selectedItems).map((productId) => {
      return { product: doc(productId), amount: 1 };
    });
    setOrderList((list) => [...list, ...chosenProducts]);
  }, []);

  // check loading
  const isDataLoading = Firebase.useIsLoading();
  if (isDataLoading) {
    return <Loader />;
  }

  // check client
  const client = doc(param.clientId);
  if (!client) {
    return <Typography variant="h4"></Typography>;
  }

  const totalPrice = orderList.reduce((total, order) => {
    return total + order.amount * order.product.price;
  }, 0);

  const closeDialog = () => setShowConfirmDialog(false);
  const gotoClientInfoPage = () => history.push(`/clients/${client.id}`);
  const gotoProductPage = () => history.push(`/clients/${client.id}/order/selectProducts`);
  const saveOrderList = async () => {
    setIsSavingOrderList(true);

    // https://cloud.google.com/firestore/docs/manage-data/transactions#batched-writes
    const batch = Firebase.batch();
    const orderId = nanoid();
    for (const order of orderList) {
      if (order.amount <= 0) {
        continue;
      }

      const remindDates = Array.from({ length: order.amount }, (_, i) => {
        return moment()
          .add(i + 2, 'months')
          .format('YYYY-MM-DD');
      }).reduce((acc, cur) => {
        const currentYear = moment(cur).year();
        if (!acc[currentYear]) acc[currentYear] = [];
        acc[currentYear].push(moment(cur).month());
        return acc;
      }, {});

      batch.set(
        Firebase.collection('purchase').doc(),
        Schema.createPurchase({
          orderId,
          clientId: client.id,
          productId: order.product.id,
          amount: order.amount,
          purchaseDate: formatDate(new Date()), // TODO: select date
          remindDates: remindDates,
          note: '', // TODO: add note
        })
      );
    }

    await batch.commit();
    action.product.clearSelectedProducts();
    gotoClientInfoPage();
  };

  return (
    <div className="panel-container">
      {/* 1. Top App Bar */}
      <AppBar position="static">
        <Toolbar>
          <IconButton color="inherit" onClick={gotoProductPage}>
            <ArrowBackIosTwoToneIcon />
          </IconButton>
          <Typography variant="h6"> ({client.lastName + client.firstName})</Typography>
        </Toolbar>
      </AppBar>

      <div className="panel-content">
        {/* 2. Order List */}
        <List>
          {orderList.map((order, index) => (
            <OrderItem key={index} order={order} orderIndex={index} setOrderList={setOrderList} />
          ))}
        </List>
      </div>

      {/* 3. Bottom App Bar: Total Amount and Confirm Purchase */}
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6">{totalPrice} </Typography>
          <div style={{ flexGrow: 1 }}></div>
          <IconButton
            color="inherit"
            disabled={totalPrice <= 0}
            onClick={() => setShowConfirmDialog(true)}
          >
            <ShoppingCartIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      {/* 4. Confirm Modal */}
      <Dialog
        fullScreen
        open={showConfirmDialog}
        onClose={closeDialog}
        container={document.getElementById('reminder-app')}
      >
        <DialogTitle onClose={closeDialog}>{totalPrice} </DialogTitle>
        <DialogContent dividers>
          {isSavingOrderList ? (
            <Loader />
          ) : (
            <List>
              {orderList
                .filter((order) => order.amount > 0)
                .map((order, index) => {
                  const { product, amount } = order;

                  return (
                    <ListItem key={index}>
                      <ListItemAvatar className="product-item-avatar">
                        <Avatar src={product.img} />
                      </ListItemAvatar>
                      <ListItemText
                        primary={`${index + 1}. ${product.name}`}
                        secondary={`$${product.price} x ${amount}`}
                      />
                    </ListItem>
                  );
                })}
            </List>
          )}
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={closeDialog} color="secondary">
            
          </Button>
          <Button onClick={saveOrderList} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default CreateOrderPage;
