import { useState } from 'react';
import { useHistory } from 'react-router-dom';

// material-ui
import Button from '@material-ui/core/Button';
import FormHelperText from '@material-ui/core/FormHelperText';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import Firebase from '../../apis/firebase';
import Loader from '../Loader';

const getErrorMessage = (error) => {
  const ERROR_MESSAGE = {
    'auth/invalid-email': '',
    'auth/user-disabled': '',
    'auth/user-not-found': '',
    'auth/wrong-password': '',
  };
  return ERROR_MESSAGE[error.code] || `${error.code}: ${error.message}`;
};

const LoginPage = () => {
  const history = useHistory();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isLogining, setIsLogining] = useState(false);
  const [error, setError] = useState({ code: '', message: '' });

  const onFieldChange = (setter) => (e) => {
    setError({ code: '', message: '' });
    setter(e.target.value);
  };

  return (
    <form
      style={{ textAlign: 'center', padding: '10px' }}
      onSubmit={async (e) => {
        e.preventDefault();

        setIsLogining(true);
        await Firebase.login(email, password).catch(setError);
        setIsLogining(false);
      }}
    >
      <Loader open={isLogining} />

      <br />
      <br />
      <Typography variant="h3" gutterBottom>
        Reminder
      </Typography>

      {/* 1. email */}
      <TextField
        style={{ width: '100%' }}
        name="email"
        type="email"
        label=""
        variant="outlined"
        InputLabelProps={{ shrink: true }}
        onChange={onFieldChange(setEmail)}
        error={/email/.test(error.code)}
      />

      <br />
      <br />

      {/* 2. password */}
      <TextField
        style={{ width: '100%' }}
        name="password"
        type="password"
        label=""
        variant="outlined"
        InputLabelProps={{ shrink: true }}
        onChange={onFieldChange(setPassword)}
        error={/password/.test(error.code)}
      />

      <br />
      <br />
      <br />
      {error.code && <FormHelperText error={true}>{getErrorMessage(error)}</FormHelperText>}
      {error.code && <br />}

      <div className="unauth-buttons">
        <Button size="large" color="primary" onClick={() => history.push('/signup')}>
          
        </Button>
        <Button size="large" color="primary" onClick={() => history.push('/forget_password')}>
          
        </Button>
        <Button
          variant="contained"
          size="large"
          type="submit"
          color="primary"
          disabled={email === '' || password === ''}
        >
          
        </Button>
      </div>
    </form>
  );
};

export default LoginPage;
