import { useEffect } from 'react';
import createStore from '../utils/createStore';

import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import 'firebase/functions';
import 'firebase/analytics';

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: 'AIzaSyB0K7kobBNLcK3zqbuRxFS8epP6KtvLns0',
  authDomain: 'reminder-app-95837.firebaseapp.com',
  projectId: 'reminder-app-95837',
  storageBucket: 'reminder-app-95837.appspot.com',
  messagingSenderId: '405021844080',
  appId: '1:405021844080:web:b7278fab45891c9b8c4ab5',
  measurementId: 'G-0DTTR6ZL70',
};

// Initialize Firebase
firebase.initializeApp(firebaseConfig);
firebase.analytics();

const collections = ['client', 'product', 'inventory', 'purchase'];
const Firebase = createStore({
  isInitializing: true,
  isLogin: false,
  user: {
    uid: '',
    email: '',
    creationTime: '',
  },
  doc: {},
  collection: collections.reduce((obj, collectName) => {
    obj[collectName] = { isLoading: true, list: [] };
    return obj;
  }, {}),
});

// 1. Subscribe Firebase Auth State Changed
const useSubscribeAuthStateChanged = () => {
  const dispatch = Firebase.useDispatch();
  useEffect(() => {
    let isMounted = true;

    firebase.auth().onAuthStateChanged(async (user) => {
      if (!isMounted) return;

      const idTokenResult = await user?.getIdTokenResult();

      const isLogin = !!user;
      dispatch((state) => {
        state.isInitializing = false;
        state.isLogin = isLogin;
        state.user.uid = isLogin ? user.uid : '';
        state.user.email = isLogin ? user.email : '';
        state.user.creationTime = isLogin ? user.metadata?.creationTime : '';
        state.user.expirationDate = idTokenResult?.claims?.expiration_date;
      });
    });

    return () => {
      isMounted = false;
    };
  }, [dispatch]);
};

// 2. useSubscribeCollection Hook
const useSubscribeCollection = (collectionName) => {
  const isLogin = Firebase.useState((state) => state.isLogin);
  const uid = Firebase.useState((state) => state.user.uid);
  const dispatch = Firebase.useDispatch();

  useEffect(() => {
    let isMounted = true;
    if (!isLogin) return;

    dispatch((state) => {
      state.collection[collectionName].isLoading = true;
    });

    firebase
      .firestore()
      .collection('user')
      .doc(uid)
      .collection(collectionName)
      .onSnapshot(
        // got snapshot
        (snap) => {
          if (!isMounted) return;

          dispatch((state) => {
            // create list
            const list = snap.docs.map((doc) => ({
              id: doc.id,
              ...doc.data(),
            }));

            // set collection list
            state.collection[collectionName] = { isLoading: false, list };

            // add item to the doc
            list.forEach((item) => {
              state.doc[item.id] = item;
            });
          });
        },

        // error handle
        (error) => {
          console.log(error.message);
        }
      );

    return () => {
      isMounted = false;
    };
  }, [dispatch, uid, isLogin, collectionName]);
};

// 3. useCollection
const useCollection = (collectionName) => {
  return Firebase.useState((state) => state.collection[collectionName]);
};

// 4. useDocument: return a function to get the document
const useDocument = () => {
  const documents = Firebase.useState((s) => s.doc);
  return (id) => {
    if (!documents.hasOwnProperty(id)) {
      console.warn(`document '${id}' does not exist`);
    }
    return documents[id];
  };
};

/**
 * 5. useIsLoading
 * @returns {boolean} isLoading
 */
const useIsLoading = () => {
  const collections = Firebase.useState((s) => s.collection);

  let isLoading = false;
  for (const name in collections) {
    isLoading |= collections[name].isLoading;
  }

  return isLoading;
};

// authentication
Firebase.useSubscribeAuthStateChanged = useSubscribeAuthStateChanged;
Firebase.login = (email, password) => firebase.auth().signInWithEmailAndPassword(email, password);
Firebase.logout = () => firebase.auth().signOut();
Firebase.signup = async (email, password) => {
  const cred = await firebase.auth().createUserWithEmailAndPassword(email, password);
  await firebase.firestore().collection('user').doc(cred.user.uid).set({
    email: cred.user.email,
  });
};
Firebase.sendPasswordResetEmail = (email) => firebase.auth().sendPasswordResetEmail(email);

// collection
Firebase.useSubscribeCollection = useSubscribeCollection;
Firebase.useCollection = useCollection;
Firebase.useDocument = useDocument;
Firebase.useIsLoading = useIsLoading;
Firebase.batch = () => firebase.firestore().batch();
Firebase.collection = (collectionName) => {
  return firebase
    .firestore()
    .collection('user')
    .doc(firebase.auth().currentUser.uid)
    .collection(collectionName);
};

// Firebase
Firebase.firebase = firebase;

export default Firebase;
